:root {
  --section-bcg-color: #fffdee;
  --section-dark-bcg-color: #f0ecd6;
  --footer-bcg-color: #0e0c07;
  --main-title-color: #fff;
  --accent-color: #f3c653;
  --accent-dark-color: #c79002;
  --btn-menu-title-color: #000;
  --transparent-btn-color: rgba(0, 0, 0, .4);
  --logo-color: #494848;
  --main-text-color: #7a7a7a;
  --light-text-color: #b4b4b4;
  --address-hero-color: #e5e5e5;
  --contacts-color: #828282;
  --defolt-animation: 250ms cubic-bezier(.4, 0, .2, 1);
}

* {
  border: 0;
  margin: 0;
  padding: 0;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: inherit;
  margin-top: 0;
  margin-bottom: 0;
}

input:focus {
  outline: rgba(0, 0, 0, 0);
}

p {
  margin: 0;
}

a {
  color: inherit;
  margin: 0;
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

ul {
  color: inherit;
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  text-decoration: none;
  list-style: none;
}

a:active, a:visited {
  color: inherit;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

textarea {
  resize: none;
}

textarea:focus {
  outline: rgba(0, 0, 0, 0);
}

button, input[type="submit"], input[type="reset"] {
  color: inherit;
  font: inherit;
  cursor: pointer;
  outline: inherit;
  background: none;
  border: none;
  padding: 0;
}

button::-moz-focus-inner {
  border: 0;
  padding: 0;
}

.container {
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}

